$primary-color: #b40c0c;

.game {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 5rem;
    min-width: 25%;

    & h2 {
        margin-bottom: 2rem;
        text-transform: uppercase;
    }

    &__image {
        background-size: cover;
        width: 20rem;
        height: 20rem;
        border: .3rem solid #000;
        margin-bottom: 2rem;
}
& button {
    font-size: 2rem;
    border: .3rem solid #000;
    color: #fff;
    height: 5rem;
    width: 25rem;
    border-radius: 100rem;
    background-color: $primary-color;
    margin-bottom: 2rem;
    cursor: pointer;

    &:hover {
        color: #fff;
    }

    &:not(:last-child) {
        margin-bottom: 2rem;
    }
}

& p {
    font-size: 3rem;
}

}