.cart {
    display: flex;
    flex-direction: column;
    align-items: center;

    & h2 {
        text-align: center;
        font-size: 3rem;
        margin-bottom: 2rem;
    }

    & p {
        font-size: 2rem;
        margin: 3rem 0;
        text-align: center;
    }

    &__button {
        border: .2rem solid #000;
        width: 20rem;
        height: 5rem;
        background-color: #b40c0c;
        border-radius: 10rem;
        color: #fff;
        font-size: 2rem;
        text-align: center;
        margin-bottom: 2rem;
        cursor: pointer;

        &:disabled {
            background-color: grey;
            cursor:not-allowed;
        }

        &:not(:last-child) {
            margin-right: 1.5rem;
            margin-left: 9rem;
        }
    }

    &__total {
        margin: 5rem 0;
        font-size: 2rem;
    }
}

.product {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 2rem;
}